@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.item-container,
.checkbox-container {
  display: inline-block;
}

.collapsed {
  display: none;
}

.checkbox-container {
  width: 100%;
  display: flex;
  align-items: center;
  text-align: left;
  justify-content: space-between;
}

.list-container :first-child(.item-container):hover {
  background-color: #dfdfdf;
}

.checkbox-container > span {
  margin-left: 11px;
}

.checkbox-input {
  width: 110%;
}

.icon {
  font-size: 1rem;
  margin: 0 0.8rem;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}

.icon.clicked {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.icon:focus,
.icon:hover,
.icon:active,
.icon:visited {
  color: rgb(255, 121, 11);
}

.item-container:hover {
  background-color: #eee;
}

.list-container,
.nestedList-container {
  display: flex;
  flex-direction: column;
}

.nestedList-container > li {
  margin-top: 0.15rem;
  margin-bottom: 0.1rem;
  margin-left: 2rem;
}

/* GLOBAL STYLES */

* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-size: 1.05rem;
}

body {
  width: 480px;
  margin: 0 0 0 2rem;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

li {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

body {
  padding: 1.1rem;
}

input {
  margin-right: 1.1rem;
}

